@import url(https://fonts.googleapis.com/css2?family=Philosopher&display=swap);
* {box-sizing: border-box;}

body { 
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

header {
  overflow: hidden;
  background-color: #f1f1f1;
  text-align: center;
  font-family: 'Philosopher';
}

.bar-box{
    /* margin: auto; */ 
    border-style: solid;
    border-radius: 1rem;
    border-width: .5rem;
    border-color: aqua;
    margin-bottom: 64px;/* footer se chipnewala area dikhana ke liye*/
}
.bar-box .horizintal-bars{
    width: 100%;
    height: 520px;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    display: -webkit-inline-flex;
    display: inline-flex;
    border-style: solid;
    border-width: .2rem;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    overflow: auto;
}
.property-box{
    width: auto;
    border-color: aqua;
    height: 560px;
    border-style: solid;
    border-width: .2rem;
}
.property-box button{
    margin-top: 4px;
}
.property-box input{
    width : 4.2rem;
}

@media screen and (max-width: 768px) {
    .property-box{
        height: auto;
    }
}

.message{
    width: 100%;
    color: rgb(243, 10, 10);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.bar{
  /* height: 50px; */
  width : 10rem;
  background-color: rgb(196, 196, 196);
  margin-left: .2rem;
  margin-right: .2rem;
  margin-top: auto;
  font-size: 1rem;
}

.active-bar{
  background-color: rgb(153, 223, 173) !important;
}

.sorted{
  background-color: rgb(78, 214, 117) !important;
}
footer {
  text-align: center;
  /* padding: 3px; */
  background-color: rgb(96, 95, 95);
  color: white;
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
}
